a,
.control {
  color: #81a2be;
}
.bfloat > svg {
  fill: #81a2be;
}
.bmodal hr {
  border-top: 1px solid #81a2be;
}
#banner {
  background: rgba(40, 42, 46, 0.7);
  border-bottom: 1px solid #282a2e;
}
b {
  color: #c5c8c6;
}
b.admin {
  color: #f00000;
}
b.moderator {
  color: #81a2be;
}
blockquote a:hover {
  color: #81a2be;
}
a:hover,
blockquote a,
.editing a:hover {
  color: #5f89ac;
}
article,
aside,
.modal,
.bmodal,
.pagination {
  background-color: #282a2e;
  border: 1px solid #282a2e;
}
article.highlight,
.preview,
.popup-menu {
  background-color: #1d1d21;
  border-color: #111;
}
article.editing {
  background-color: #cec952;
  border-color: #aa3;
  color: black;
}
article.editing a,
article.editing b,
article.editing header {
  color: #222;
}
article.editing em {
  color: #656d28;
}
article.editing li {
  color: #ddd;
}
article.editing .themed {
  color: #333;
}
body {
  background-color: #1d1f21;
  color: #c5c8c6;
}
body,
pre,
.themed {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10pt;
}
.embed {
  background-color: white;
}
em {
  color: #b5bd68;
}
h1 {
  font: bolder 28px Tahoma;
  letter-spacing: -2px;
}
h2 {
  font-family: Tahoma;
}
h3 {
  color: #ce52c9;
}
hr {
  border-top: 1px solid #282a2e;
}
.popup-menu li:hover {
  background-color: #136;
}
.omit {
  color: #707070;
}
#lock {
  background-image: url('ui/invlock.svg');
  background-size: 1em 1em;
}
.themed {
  color: #999;
}
.option_tab_sel a.tab_sel {
  color: #add8e6;
}
